<template>
  <div class="name-item">
    <van-nav-bar
      title="设置昵称"
      left-text="取消"
      right-text="完成"
      @click-left="$router.back()"
      @click-right="editData"
    >
    </van-nav-bar>
    <!-- 输入框 -->
    <div class="field-item">
      <van-field
        v-model="value"
        placeholder="请输入昵称"
        maxlength="7"
        show-word-limit
        autosize
      />
    </div>
    <!-- <van-loading size="24" /> -->
  </div>
</template>

<script>
import { editProfile, getProfile } from '@/api/user'
export default {
  name: 'EditNameIndex',
  data () {
    return {
      value: ''
    }
  },
  created () {
    this.getUserData()
  },
  methods: {
    async getUserData () {
      const res = await getProfile()
      // console.log(res)
      this.value = res.data.name
    },
    async editData () {
      this.$toast.loading({
        message: '保存中...',
        forbidClick: true, // 紧张背景点击
        duration: 0 // 持续展示
      })
      const res = await editProfile({
        name: this.value
      })
      // console.log(res)
      if (res.message === 'OK') {
        this.$toast.success('修改昵称成功')
      } else {
        this.$toast('修改失败')
      }
      this.value = ''
      this.$router.back()
    }
  }

}
</script>

<style lang="scss" scoped>
.name-item {
  .field-item {
    width: 710px;
    height: 210px;
    padding: 20px;
    .van-cell {
      width: 100%;
      ::v-deep .van-field__body {
        height: 98px;
        .text {
          height: 98px;
        }
      }
    }
  }
}
</style>
